<template>
  <div>Banana</div>
</template>

<script>
import {reactive,toRefs,onMounted,onUnmounted} from '@vue/composition-api'
export default {
  setup (props,{root}) {
    const state = reactive({

    })
    onMounted (() =>{
      root.$store.dispatch('hideNav',false) //初始化隐藏
      // root.$store.commit('setIsNavShow',false)
    })
    onUnmounted (() =>{
      root.$store.dispatch('showNav',true) //离开显示
      // root.$store.commit('setIsNavShow',true)
    })
    return {
      ...toRefs(state)
    }
  }
}
</script>

